<script lang="ts" setup>
import { onMounted, ref } from 'vue'
import Expenses from './pages/Expenses.vue'
import { useForm, useToast } from 'vuestic-ui'
import { deleteVehicle, getAllVehicles, newVehicle } from '../../core/requests/Vehicles'
import { deleteDriver, getAllDrivers, newDriver, updateDriver } from '../../core/requests/Drivers'
import { deleteCurrency, getAllCurrencies } from '../../core/requests/Currencies'
import { deleteService, getAllServices, newService, updateService } from '../../core/requests/Services'
import { validators } from '../../services/utils'
import { onlyNumbers, onlyNumbersNoDigits } from '../../core/helpers'
import { ceil } from 'lodash'

const vOnlyDigits = onlyNumbers
const vOnlyNumbers = onlyNumbersNoDigits

const { notify } = useToast()
const vehiclesColumns = [
  { key: 'Vehiculo', sortable: true },
  { key: 'Tipo', sortable: true },
  { key: 'Fabricación', sortable: true },
  { key: 'Capacidad', sortable: true },
  { key: 'Gal/Hr', sortable: true },
  { key: 'Maquina Seca', sortable: true },
  { key: 'Acciones', width: 80 },
]
const driversColumns = [
  { key: 'Tipo Conductor', sortable: true },
  { key: 'Tarifa', sortable: true },
  { key: 'Tarifa Nocturna', sortable: true },
  { key: 'Tarifa Domingos y Feriados', sortable: true },
  { key: 'Acciones', width: 80 },
]
const servicesColumns = [
  { key: 'Servicio', sortable: true },
  { key: 'Descripción', sortable: true },
  { key: 'Tarifa base', sortable: true },
  { key: 'Acciones', width: 80 },
]
const vehicles = ref([])
const drivers = ref([])
const services = ref([])
const currencies = ref([])
const itemSelected = ref(null)
const showNewVehicle = ref(false)
const loadingNewVehicle = ref(false)
const showNewDriver = ref(false)
// const showNewCurrency = ref(false)
const showNewService = ref(false)
const showConfirm = ref(false)
const isDeleting = ref(false)
const selectedType: any = ref(null)
const formVehicle: any = ref({})
const formDriver: any = ref({})
// const formCurrency: any = ref({})
const formService: any = ref({})
const { validate, reset: vehicleReset } = useForm('fgVehicle')
const { validate: driverValidate, reset: driverReset } = useForm('fgDriver')
// const { validate: currencyValidate, reset: currencyReset } = useForm('fgCurrency')
const { validate: serviceValidate, reset: serviceReset } = useForm('fgService')

onMounted(() => {
  getVehicles()
  getDrivers()
  getCurrencies()
  getServices()
})

const loadingServices = ref(false)
async function getServices() {
  loadingServices.value = true
  const servicesData: any = await getAllServices()
  services.value = servicesData.map((i: any) => ({
    ...i,
    Servicio: i.service,
    Descripción: i.description,
    'Tarifa base': i.fee ? `S/ ${i.fee || '0.00'}` : '-',
  }))
  loadingServices.value = false
}

const loadingVehicles = ref(false)
async function getVehicles() {
  loadingVehicles.value = true
  const vehiclesData: any = await getAllVehicles()
  vehicles.value = vehiclesData.map((i: any) => ({
    ...i,
    Vehiculo: i.name,
    Tipo: i.type,
    Fabricación: i.year,
    Capacidad: i.capacity,
    'Gal/Hr': `${i.cost || 0}`,
    'Maquina Seca': i.fee ? `S/ ${i.fee || '0.00'}` : '-',
  }))
  loadingVehicles.value = false
}

const loadingDrivers = ref(false)
async function getDrivers() {
  loadingDrivers.value = true
  const driversData: any = await getAllDrivers()
  drivers.value = driversData
    .map((i: any) => {
      if (i.fee > 0) {
        return {
          ...i,
          'Tipo Conductor': i.type,
          Tarifa: i.fee ? `S/ ${i.fee || '0.00'}` : '-',
          'Tarifa Nocturna': i.fee_1 ? `S/ ${i.fee_1 || '0.00'}` : '-',
          'Tarifa Domingos y Feriados': i.fee_2 ? `S/ ${i.fee_2 || '0.00'}` : '-',
        }
      }
    })
    .filter((f: any) => f)
  loadingDrivers.value = false
}

async function getCurrencies() {
  const currenciesData: any = await getAllCurrencies()
  currencies.value = currenciesData.map((i: any) => ({
    ...i,
    Moneda: i.label,
    'Tipo de Cambio': i.value,
  }))
}

const doDelete = async () => {
  if (selectedType.value === 'vehicles') {
    await deleteVehicle(itemSelected.value)
    await getVehicles()
  }

  if (selectedType.value === 'currencies') {
    await deleteCurrency(itemSelected.value)
    await getCurrencies()
  }

  if (selectedType.value === 'drivers') {
    await deleteDriver(itemSelected.value)
    await getDrivers()
  }

  if (selectedType.value === 'service') {
    await deleteService(itemSelected.value)
    await getServices()
  }

  selectedType.value = null
  showConfirm.value = false
}
const doNewVehicle = async () => {
  loadingNewVehicle.value = true
  try {
    validate()
    await newVehicle({
      ...formVehicle.value,
    })

    await getVehicles()

    notify({
      message: 'Vehiculo registrado correctamente',
      color: 'success',
    })

    loadingNewVehicle.value = false
    vehicleReset()
    showNewVehicle.value = false
  } catch (e: any) {
    console.log(e)
  }
}
const loadingNewDriver = ref(false)
const doNewDriver = async () => {
  try {
    driverValidate()
    loadingNewDriver.value = true
    const id = selectedDriver.value?.id
    if (id) {
      await updateDriver(id, {
        ...formDriver.value,
      })
    } else {
      await newDriver({
        ...formDriver.value,
      })
    }

    await getDrivers()

    notify({
      message: 'Conductor registrado correctamente',
      color: 'success',
    })

    loadingNewDriver.value = false
    selectedDriver.value = null

    driverReset()
    showNewDriver.value = false
  } catch (e: any) {
    console.log(e)
  }
}
const loadingNewService = ref(false)
const doNewService = async () => {
  try {
    serviceValidate()
    loadingNewService.value = true
    if (selectedService.value?.id) {
      await updateService(selectedService.value.id, {
        ...formService.value,
      })
    } else {
      await newService({
        ...formService.value,
      })
    }

    await getServices()

    notify({
      message: 'Servicio registrado correctamente',
      color: 'success',
    })
    loadingNewService.value = false

    serviceReset()
    formService.value = {}
    showNewService.value = false
  } catch (e: any) {
    console.log(e)
  }
}
const needConfirm = (id: any, type: string) => {
  showConfirm.value = id ? true : false
  itemSelected.value = id
  selectedType.value = type
}
const showViewService = ref(false)
const closeViewService = () => {
  formService.value = {}
  showViewService.value = false
  selectedService.value = null
}

const vehicleTypes = [
  'Ligero',
  '2 Ejes',
  '3 Ejes',
  '4 Ejes',
  '5 Ejes',
  '6 Ejes',
  '7 Ejes',
  '8 Ejes',
  '9 Ejes',
  '10 Ejes',
  '11 Ejes',
  '12 Ejes',
  '13 Ejes',
  '14 Ejes',
  '15 Ejes',
]

const selectedVehicle: any = ref(null)
const sowUpdate = (data: any) => {
  if (data) {
    selectedVehicle.value = data
    showNewVehicle.value = true

    formVehicle.value.name = data?.name
    formVehicle.value.type = data?.type
    formVehicle.value.capacity = data?.capacity
    formVehicle.value.year = data?.year
    formVehicle.value.fee = data?.fee
    formVehicle.value.cost = data?.cost
  } else {
    selectedVehicle.value = null
    showNewVehicle.value = false

    formVehicle.value.name = ''
    formVehicle.value.type = ''
    formVehicle.value.year = ''
    formVehicle.value.capacity = ''
    formVehicle.value.fee = ''
    formVehicle.value.cost = ''
  }
}

const selectedDriver: any = ref(null)
const showUpdateDriver = (data: any) => {
  if (data) {
    selectedDriver.value = data
    showNewDriver.value = true

    formDriver.value.type = data?.type
    formDriver.value.fee = data?.fee || 0
    formDriver.value.fee_1 = data?.fee_1 || 0
    formDriver.value.fee_2 = data?.fee_2 || 0
  } else {
    selectedDriver.value = null
    showNewDriver.value = false

    formDriver.value.type = ''
    formDriver.value.fee = ''
    formDriver.value.fee_1 = ''
    formDriver.value.fee_2 = ''
  }
}

const selectedService: any = ref(null)
const showUpdateService = (data: any) => {
  if (data) {
    selectedService.value = data
    showNewService.value = true
    formService.value.specifications = data?.specifications
    formService.value.service = data?.service
    formService.value.description = data?.description
    formService.value.fee = data?.fee
  } else {
    selectedService.value = data
    showNewService.value = false
    formService.value.specifications = ''
    formService.value.service = ''
    formService.value.description = ''
    formService.value.fee = ''
  }
}
</script>

<template>
  <div class="flex flex-col space-y-6 md:space-y-4">
    <VaCard>
      <VaCardTitle class="flex justify-between">
        Vehiculos
        <VaButton class="ml-3 pr-2 pl-1" icon="add" color="primary" @click="showNewVehicle = true">Agregar</VaButton>
      </VaCardTitle>
      <VaCardContent>
        <div class="flex flex-col p-4 bg-backgroundSecondary rounded-lg">
          <VaDataTable :items="vehicles" :columns="vehiclesColumns" class="text-xs" :loading="loadingVehicles">
            <template #cell(Acciones)="{ row }">
              <div class="flex gap-1">
                <VaButton icon="edit" color="primary" round @click="sowUpdate(row?.rowData)"></VaButton>
                <VaButton
                  icon="delete"
                  color="danger"
                  round
                  @click="needConfirm(row?.rowData?.id, 'vehicles')"
                ></VaButton>
              </div>
            </template>
          </VaDataTable>
        </div>
      </VaCardContent>
    </VaCard>

    <VaModal
      v-model="showNewVehicle"
      hide-default-actions
      size="300px"
      close-button
      blur
      no-dismiss
      @close="sowUpdate(null)"
      @ok="doNewVehicle"
    >
      <div>
        <h4 class="text-base mb-4">Nuevo vehiculo</h4>
        <div class="flex flex-col">
          <VaForm ref="fgVehicle" immediate class="gap-2">
            <VaInput
              v-model="formVehicle.name"
              stateful
              name="name"
              clearable
              label="Nombre de Vehiculo"
              class="mb-3"
              :rules="[validators.required]"
            />

            <VaSelect
              v-model="formVehicle.type"
              class="w-full text-xs mb-3"
              label="Tipo de Vehiculo"
              :options="vehicleTypes"
              placeholder="Selecciona un tipo"
              :rules="[validators.required]"
            />

            <VaInput
              v-model="formVehicle.capacity"
              v-only-digits
              stateful
              name="capacity"
              type="number"
              clearable
              label="Capacidad"
              class="mb-3"
              :rules="[validators.required]"
            />

            <VaInput
              v-model="formVehicle.year"
              v-only-numbers
              stateful
              name="year"
              type="text"
              clearable
              label="Año"
              class="mb-3"
              :rules="[validators.required]"
            />

            <VaInput
              v-model="formVehicle.fee"
              v-only-digits
              stateful
              name="fee"
              clearable
              label="Maquina Seca"
              class="mb-3"
              :rules="[validators.required]"
              @change="
                () => {
                  formVehicle.fee = ceil(formVehicle.fee, 2)
                }
              "
            >
              <template #prependInner>
                <span class="font-bold text-sm">S/</span>
              </template>
            </VaInput>

            <VaInput
              v-model="formVehicle.cost"
              v-only-digits
              stateful
              name="cost"
              clearable
              label="Tarifa por Kilometro"
              class="mb-3"
              :rules="[validators.required]"
              @change="
                () => {
                  formVehicle.cost = ceil(formVehicle.cost, 2)
                }
              "
            >
              <template #prependInner>
                <span class="font-bold text-sm">S/</span>
              </template>
            </VaInput>
          </VaForm>
        </div>
        <div class="flex">
          <VaButton class="pr-2 pl-1" icon="save" color="primary" :loading="loadingNewVehicle" @click="doNewVehicle">
            <span class="ml-2">Guardar</span>
          </VaButton>
        </div>
      </div>
    </VaModal>

    <VaCard>
      <VaCardTitle class="flex justify-between">
        Conductores
        <VaButton class="ml-3 pr-2 pl-1" icon="add" color="primary" @click="showNewDriver = true">Agregar</VaButton>
      </VaCardTitle>
      <VaCardContent>
        <div class="flex flex-col p-4 bg-backgroundSecondary rounded-lg">
          <VaDataTable :items="drivers" :columns="driversColumns" class="text-xs" :loading="loadingDrivers">
            <template #cell(Acciones)="{ row }">
              <div class="flex gap-1">
                <VaButton icon="edit" color="primary" round @click="showUpdateDriver(row?.rowData)"></VaButton>
                <VaButton
                  icon="delete"
                  color="danger"
                  round
                  @click="needConfirm(row?.rowData?.id, 'drivers')"
                ></VaButton>
              </div>
            </template>
          </VaDataTable>
        </div>
      </VaCardContent>
    </VaCard>

    <VaModal
      v-model="showNewDriver"
      hide-default-actions
      size="300px"
      close-button
      blur
      no-dismiss
      @close="showUpdateDriver(null)"
      @ok="doNewDriver"
    >
      <div>
        <h4 class="text-base mb-4">Nuevo tipo de conductor</h4>
        <div class="flex flex-col">
          <VaForm ref="fgDriver" immediate class="gap-2">
            <VaInput
              v-model="formDriver.type"
              stateful
              name="type"
              clearable
              label="Tipo de chofer"
              class="mb-3"
              :rules="[validators.required]"
            />

            <VaInput
              v-model="formDriver.fee"
              v-only-digits
              stateful
              name="fee"
              clearable
              label="Tarifa por Hora"
              class="mb-5"
              :rules="[validators.required]"
              @change="
                () => {
                  formDriver.fee = ceil(formDriver.fee, 2)
                }
              "
            >
              <template #prependInner>
                <span class="pr-2 font-bold text-sm">S/</span>
              </template>
            </VaInput>

            <VaInput
              v-model="formDriver.fee_1"
              v-only-digits
              stateful
              name="fee_1"
              clearable
              label="Tarifa Nocturna"
              class="mb-5"
              @change="
                () => {
                  formDriver.fee = ceil(formDriver.fee_1, 2)
                }
              "
            >
              <template #prependInner>
                <span class="pr-2 font-bold text-sm">S/</span>
              </template>
            </VaInput>

            <VaInput
              v-model="formDriver.fee_2"
              v-only-digits
              stateful
              name="fee_2"
              clearable
              label="Tarifa Domingos y Feriados"
              class="mb-5"
              @change="
                () => {
                  formDriver.fee = ceil(formDriver.fee_2, 2)
                }
              "
            >
              <template #prependInner>
                <span class="pr-2 font-bold text-sm">S/</span>
              </template>
            </VaInput>
          </VaForm>
        </div>
        <div class="flex">
          <VaButton class="pr-2 pl-1" icon="save" color="primary" :loading="loadingNewDriver" @click="doNewDriver">
            <span class="ml-2">Guardar</span>
          </VaButton>
        </div>
      </div>
    </VaModal>

    <VaCard>
      <VaCardTitle class="flex justify-between">
        Servicios
        <VaButton class="ml-3 pr-2 pl-1" icon="add" color="primary" @click="showNewService = true">Agregar</VaButton>
      </VaCardTitle>
      <VaCardContent>
        <div class="flex flex-col p-4 bg-backgroundSecondary rounded-lg">
          <VaDataTable :items="services" :columns="servicesColumns" class="text-xs" :loading="loadingServices">
            <template #cell(Acciones)="{ row }">
              <div class="flex gap-1">
                <!-- <VaButton icon="visibility" color="info" round @click="viewService(row?.rowData)"></VaButton> -->
                <VaButton icon="edit" color="primary" round @click="showUpdateService(row?.rowData)"></VaButton>
                <VaButton
                  icon="delete"
                  color="danger"
                  round
                  @click="needConfirm(row?.rowData?.id, 'service')"
                ></VaButton>
              </div>
            </template>
          </VaDataTable>
        </div>
      </VaCardContent>
    </VaCard>

    <VaModal
      v-model="showNewService"
      hide-default-actions
      size="400px"
      close-button
      no-dismiss
      blur
      @beforeClose="closeViewService"
    >
      <div>
        <h4 v-if="!showViewService" class="text-base mb-4">Nuevo Servicio</h4>
        <h4 v-if="showViewService" class="text-base mb-4">Detalle de Servicio</h4>
        <div class="flex flex-col">
          <VaForm ref="fgService" immediate class="gap-2">
            <VaInput
              v-model="formService.service"
              stateful
              name="service"
              clearable
              label="Tipo de servicio"
              class="mb-3"
              :readonly="showViewService"
              :rules="[validators.required]"
            />

            <div class="columns-2">
              <div>
                <VaInput
                  v-model="formService.description"
                  stateful
                  name="description"
                  clearable
                  label="Detalle del servicio"
                  class="mb-3"
                  :readonly="showViewService"
                  :rules="[validators.required]"
                />
              </div>

              <div>
                <VaInput
                  v-model="formService.fee"
                  v-only-digits
                  stateful
                  name="fee"
                  clearable
                  label="Tarifa base"
                  class="mb-3"
                  :readonly="showViewService"
                  @change="
                    () => {
                      formService.fee = ceil(formService.fee, 2)
                    }
                  "
                >
                  <template #prependInner>
                    <span class="pr-2 font-bold text-sm">S/</span>
                  </template>
                </VaInput>
              </div>
            </div>

            <div class="mb-5">
              <p style="font-size: 11px; font-weight: 600; color: var(--va-primary)">Condiciones especificas:</p>
              <QuillEditor
                v-model:content="formService.specifications"
                theme="snow"
                content-type="html"
                :toolbar="['bold', 'italic']"
              />
            </div>
          </VaForm>
        </div>
        <div v-if="!showViewService" class="flex">
          <VaButton class="pr-2 pl-1" icon="save" color="primary" :loading="loadingNewService" @click="doNewService">
            <span class="ml-2">Guardar</span>
          </VaButton>
        </div>
      </div>
    </VaModal>

    <Expenses />
  </div>

  <VaModal v-model="showConfirm" size="250px" hide-default-actions no-dismiss blur>
    <p class="text-sm">¿Deseas continuar?</p>

    <template #footer>
      <VaButton size="small" preset="secondary" class="mr-1" @click="needConfirm(null, '')"> Cancelar</VaButton>
      <VaButton size="small" :loading="isDeleting" @click="doDelete"> Continuar</VaButton>
    </template>
  </VaModal>
</template>
