<script lang="ts" setup>
import { onMounted, ref } from 'vue'
import { useForm, useToast } from 'vuestic-ui'
import { deleteExpense, getAllExpenses, newExpense } from '../../../core/requests/Expenses'
import { validators } from '../../../services/utils'
import { ceil } from 'lodash'
import { onlyNumbers } from '../../../core/helpers'

const vOnlyDigits = onlyNumbers

const { notify } = useToast()
const columns = [
  { key: 'Tipo', sortable: true },
  { key: 'Valor', sortable: true },
  { key: 'Acciones', width: 80 },
]
const items = ref([])
const itemSelected = ref(null)
const showNew = ref(false)
const showConfirm = ref(false)
const isDeleting = ref(false)
const isLoading = ref(false)
const form: any = ref({})
const { validate, reset } = useForm('formExpense')

onMounted(async () => {
  await getData()
})

async function getData() {
  isLoading.value = true
  const data: any = await getAllExpenses()
  items.value = data.map((i: any) => ({
    ...i,
    Tipo: i.name,
    Valor: `S/ ${i.value || '0.00'}`,
  }))
  isLoading.value = false
}

const doDelete = async () => {
  await deleteExpense(itemSelected.value)
  await getData()
  showConfirm.value = false
}
const doNew = async () => {
  try {
    validate()
    isLoading.value = true
    await newExpense({
      ...form.value,
    })

    await getData()
    isLoading.value = false
    reset()

    notify({
      message: 'Registrado correctamente',
      color: 'success',
    })

    showNew.value = false
  } catch (e: any) {
    console.log(e)
  }
}
const needConfirm = (id: any) => {
  showConfirm.value = id ? true : false
  itemSelected.value = id
}
</script>

<template>
  <div class="flex flex-col space-y-6 md:space-y-4">
    <VaCard>
      <VaCardTitle class="flex justify-between">
        Viaticos
        <VaButton class="ml-3 pr-2 pl-1" icon="add" color="primary" @click="showNew = true">Agregar</VaButton>
      </VaCardTitle>
      <VaCardContent>
        <div class="flex flex-col p-4 bg-backgroundSecondary rounded-lg">
          <VaDataTable :items="items" :columns="columns" class="text-xs" :loading="isLoading">
            <template #cell(Acciones)="{ row }">
              <div class="flex gap-1">
                <VaButton icon="delete" color="danger" round @click="needConfirm(row?.rowData?.id)"></VaButton>
              </div>
            </template>
          </VaDataTable>
        </div>
      </VaCardContent>
    </VaCard>

    <VaModal v-model="showNew" hide-default-actions size="300px" close-button blur no-dismiss @ok="doNew">
      <div>
        <h4 class="text-base mb-4">Nuevo Viatico</h4>
        <div class="flex flex-col">
          <VaForm ref="formExpense" immediate class="gap-2">
            <VaInput
              v-model="form.name"
              stateful
              name="name"
              clearable
              label="Tipo de viatico"
              class="mb-4"
              :rules="[validators.required]"
            />

            <VaInput
              v-model="form.value"
              v-only-digits
              stateful
              name="value"
              clearable
              label="Valor promedio"
              class="mb-5"
              :rules="[validators.required]"
              @change="
                () => {
                  form.value = ceil(form.value, 2)
                }
              "
            >
              <template #prependInner>
                <span class="pr-2 font-bold text-sm">S/</span>
              </template>
            </VaInput>
          </VaForm>
        </div>
        <div class="flex">
          <VaButton class="pr-2 pl-1" icon="save" color="primary" :loading="isLoading" @click="doNew">
            <span class="ml-2">Registrar</span>
          </VaButton>
        </div>
      </div>
    </VaModal>
  </div>

  <VaModal v-model="showConfirm" size="250px" hide-default-actions no-dismiss blur>
    <p class="text-sm">¿Deseas continuar?</p>

    <template #footer>
      <VaButton size="small" preset="secondary" class="mr-1" @click="needConfirm(null)"> Cancelar</VaButton>
      <VaButton size="small" :loading="isDeleting" @click="doDelete"> Continuar</VaButton>
    </template>
  </VaModal>
</template>
